.contactsContainer{
    min-height: 400px;
}

.contactsArea {
    margin: auto;
    width: 250px;
    padding-top: 50px;
    padding-bottom: 30px;
}

.contactsArea h4, .contactsArea ul li {
    background-color: #580e16;
    color: #ffffff;
    padding: 0 5px;
    border-radius: 5px;
    box-shadow: #ffffff 0 0 10px;
}

.contactsArea ul li {
    margin-bottom: 5px;
}

.contacts {
    list-style-type: none;
    text-align: left;
    padding: 0;
}


.carousel {
    max-width: 400px;
    margin: auto;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: #ffffff 0 0 20px;
}

.carouselImage {
    width: 100%;
}
